/* eslint-disable no-return-assign */
import React from 'react'
import Helmet from 'react-helmet'
import styled from 'styled-components'
import { IoSocialJavascript } from 'react-icons/lib/io'
import config from '../../data/SiteConfig'
import SkillsList from '../components/Layout/SkillsList'
import SiteHeader from '../components/Layout/Header'
import skillsData from '../../data/skills.json'

class Skills extends React.Component {
  render() {
    return (
      <div className="skills-container">
        <Helmet title={'Skills Optic'}>
          <link rel="stylesheet" href="https://cdn.rawgit.com/konpa/devicon/df6431e323547add1b4cf45992913f15286456d3/devicon.min.css" />
        </Helmet>
        <main>
          <HeaderContainer>
            <SiteHeader location={this.props.location} />
          </HeaderContainer>
          <IndexHeadContainer>
            <Hero>
              <div style={{textAlign: 'center' }}>
                <h1 style={{color:'white'}}>Optic Skills</h1>
                <LanguagesToC languages={skillsData.languages} />
              </div>
            </Hero>
          </IndexHeadContainer>
          <BodyContainer>
            <SkillsList languages={skillsData} />
          </BodyContainer>
        </main>
      </div>
    )
  }
}

const LanguagesToC = ({ languages }) => (
  <LanguagesToCContainer>
    {Object.keys(languages).map((language) => (
      <a key={language} href={`#link-${language}`}><IoSocialJavascript /> {language}</a>
    ))}
  </LanguagesToCContainer>
)
const LanguagesToCContainer = styled.ul`
  max-width: 700px;
  margin: 0 auto;
  a {
    font-size: 2rem;
    text-decoration: underline;
    color: white;
    display: inline-block;
    margin: 0 20px;
  }
`

const BodyContainer = styled.div`
  padding: ${props => props.theme.sitePadding};
  padding-top: 0;
`
const HeaderContainer = styled.div`
  grid-column: 1 / 3;
  grid-row: 1 / 2;
  z-index: 2;
  position: fixed;
  width: 100%;
  
  @media screen and (max-width: 600px) {
    order: 1;
  }
`
const IndexHeadContainer = styled.div`
  background: ${props => props.theme.brand};
  padding: ${props => props.theme.sitePadding};
  text-align: center;

  background-image: url('/home-images/skills-page.png');
  padding: 50px 0;
  background-size: 1142px 300px;
  background-repeat: repeat-x;
  background-position-y: 40px;
`
const Hero = styled.div`
  padding: 50px 0;
  & > h1 {
    font-weight: 600;
  }
`

export default Skills



