import React from 'react'
import styled from 'styled-components'
import Link from 'gatsby-link'

class SkillsList extends React.Component {
  render() {
    const { languages } = this.props.languages
    return (
      <LanguageList languages={languages} />
    )
  }
}

const LanguageList = ({ languages }) => (
  <LanguageListContainer>
    {Object.keys(languages).map((language, key) => (
      <LanguageContainer key={key}>
        <LanguageTitle id={`link-${language}`}>{language}</LanguageTitle>
        <CategoryList categories={languages[language]} />
      </LanguageContainer>
    ))}
  </LanguageListContainer>
)

const CategoryList = ({ categories }) => (
  <CategoryListContainer>
    {categories.map((category, index) => (
      <CategoryContainer key={index}>
        <CategoryTitle>{category.name}</CategoryTitle>
        <SkillList skills={category.skills} />
      </CategoryContainer>
    ))}
  </CategoryListContainer>
)

const SkillList = ({ skills }) => (
  <SkillsListContainer>
    {skills.map((skill, index) => (
      <li key={index}>
        <Link to={skill.link}>{skill.name}</Link>
      </li>
    ))}
  </SkillsListContainer>
)

const LanguageListContainer = styled.div`
  max-width: 700px;
  margin: 0 auto;
`
const LanguageContainer = styled.div``
const LanguageTitle = styled.h2`
  text-align: left;
  margin-top: 50px;
`

const CategoryListContainer = styled.div`
  display: flex;
  flex-direction: row;
  margin-bottom: 25px;

  @media screen and (max-width:600px) {
    flex-wrap: wrap;
  }
`
const CategoryContainer = styled.div`
  flex: 1 33%;

  @media screen and (max-width: 600px) {
    flex: 1 50%;
  }
`
const CategoryTitle = styled.h4`
  text-decoration: underline;
  margin: 0;
`

const SkillsListContainer = styled.ul`
  margin-top: 0;
`

export default SkillsList



